import { Component, OnInit } from '@angular/core';
import { CompanyDepartemenService, DirektoriVPegawaiPosisiService, CompanyDivisiService } from 'src/app/sdk/hr/services';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { Departemen } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private companyDepartemenService: CompanyDepartemenService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private companyDivisiService: CompanyDivisiService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    ngOnInit() {
        this.getData()
    }


    sortKey: string | null = '';
    sortValue: string | null = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.getData();
    }
    searchValue: string = "";


    listOfDisplayData: Departemen[] = []
    listOfData: Departemen[] = []
    getData() {
        this.spinner.show()
        this.companyDepartemenService.getCompanyDepartemens(JSON.stringify({sortKey: this.sortKey, sortValue: this.sortValue, search: this.searchValue})).subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;
                this.spinner.hide()

                this.listOfDisplayData.forEach((departemen, index) =>{
                    this.getTotalPegawai(index, departemen.id_departemen)
                    this.getTotalDivisi(index, departemen.id_departemen)
                })

            }
        )
    }

    reset(): void {
        this.searchValue = '';
        this.getData();
    }

    getTotalPegawai(index, id_departemen) {
        let param = {
            id_departemen: id_departemen,
            total: true
        }
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisis(JSON.stringify(param)).subscribe(
            data => {
                this.listOfDisplayData[index]['total_peagwai'] = data.data[0]['total']
            }
        )
    }

    getTotalDivisi(index, id_departemen) {
        let param = {
            id_departemen: id_departemen,
            total: true
        }
        this.companyDivisiService.getCompanyDivisis(JSON.stringify(param)).subscribe(
            data => {
                this.listOfDisplayData[index]['total_divisi'] = data.data[0]['total']
            }
        )
    }




    // search(): void {
    //     const data = this.listOfData.filter(item => this.filterFuctionService.filterFunc(item, this.searchValue));
    //     /** sort data **/
    //     if (this.sortName && this.sortValue) {
    //         this.listOfDisplayData = data.sort((a, b) =>
    //             this.sortValue === 'ascend'
    //                 ? a[this.sortName!] > b[this.sortName!]
    //                     ? 1
    //                     : -1
    //                 : b[this.sortName!] > a[this.sortName!]
    //                     ? 1
    //                     : -1
    //         );
    //     } else {
    //         this.listOfDisplayData = data;
    //     }
    // }

    delete(id) {
        this.spinner.show();
        this.companyDepartemenService.deleteCompanyDepartemensId(id).subscribe(
            data => {
                this.getData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }


}
