import { Router } from '@angular/router';
import { CompanyDepartemenService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { FormGroupTypeSafe, FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Departemen } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyDepartemenService: CompanyDepartemenService,

        private router: Router
    ) { }

    dep: Departemen = {};
    ngOnInit() {
        this.form();
    }

    depForm: FormGroupTypeSafe<Departemen>;
    form() {
        this.depForm = this.fb.group<Departemen>(
            {
                nama_departemen: new FormControl('', [Validators.required]),
                kode_departemen: new FormControl('', [Validators.required]),
                status: new FormControl('', [Validators.required])
            }
        )
    }

    simpan() {
        if (this.depForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            return;
        }

        this.dep = this.depForm.value;
        this.companyDepartemenService.postCompanyDepartemens(this.dep).subscribe(
            data => {
                this.goToBack()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/departement/list'])
    }

}
;
