import { NgxSpinnerService } from 'ngx-spinner';
import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { CompanyDepartemenService } from 'src/app/sdk/hr/services';
import { Router, ActivatedRoute } from '@angular/router';
import { Departemen } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyDepartemenService: CompanyDepartemenService,

        private router: Router,
        private spinner: NgxSpinnerService,
        private activatedRoute: ActivatedRoute
    ) { }

    id_departemen: any = 0;
    dep: Departemen = {};
    ngOnInit() {

        this.id_departemen = this.activatedRoute.snapshot.paramMap.get('id');

        this.form();
        this.getData()
    }

    getData() {
        this.spinner.show()
        this.companyDepartemenService.getCompanyDepartemensId(this.id_departemen).subscribe(
            data => {
                this.depForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }


    depForm: FormGroupTypeSafe<Departemen>;
    form() {
        this.depForm = this.fb.group<Departemen>(
            {
                id_departemen: new FormControl(''),
                updated_at: new FormControl(''),
                created_at: new FormControl(''),
                nama_departemen: new FormControl('', [Validators.required]),
                status: new FormControl(''),
                kode_departemen: new FormControl(''),
                jumlah_divisi: new FormControl(''),
                jumlah_pegawai: new FormControl(''),
            }
        )
    }

    simpan() {
        if (this.depForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            return;
        }

        this.dep = this.depForm.value;

        this.companyDepartemenService.putCompanyDepartemensId(
            {
                id: this.id_departemen,
                body: this.dep
            }
        ).subscribe(
            data => {
                this.goToBack()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/departement/list'])
    }

}
